.theViettel {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .content {
    // padding: 20px;
    margin-top: 30px;
    padding-top: 50px;
    padding-bottom: 70px;
    .title {
      letter-spacing: 1px;
      font-size: 20px;
      color: rgb(0, 153, 153);
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .content-silde {
      margin-top: 50px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-self: center;
      gap: 30px;
      color: white;
      .silde1 {
        border-radius: 30px;
        background-color: #f91111;
        padding: 20px;
        width: 100%;
        min-width: 300px;
        max-width: 550px;
        display: flex;
        gap: 20px;
        align-items: center;
        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          background-color: aqua;
          border: 1px solid #f91111;
        }
        .silde1-content {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 10px;
          p {
            text-align: center;
            color: white;
          }
          .silde1-content-hotline {
            a {
              cursor: pointer;
              width: 180px;
              background-color: white;
              text-decoration: none;
              color: black;
              display: flex;
              align-items: center;
              justify-content: space-around;

              padding: 10px;
              border-radius: 40px;
              p {
                color: black;
                font-weight: 600;
                // letter-spacing: 1px;
              }
              &:hover {
                background-color: #dadcde;
              }
            }
          }
        }
      }
      .silde2 {
        border-radius: 30px;
        background-color: #f4a617;
        padding: 20px;
        width: 100%;
        min-width: 300px;
        max-width: 550px;
        display: flex;
        gap: 20px;
        align-items: center;
        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          background-color: aqua;
          border: 1px solid #f4a617;
        }
        .silde2-content {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 10px;
          p {
            text-align: center;
            color: white;
          }
          .silde2-content-hotline {
            a {
              cursor: pointer;
              width: 180px;
              background-color: white;
              text-decoration: none;
              color: black;
              display: flex;
              align-items: center;
              justify-content: space-around;

              padding: 10px;
              border-radius: 40px;
              p {
                color: black;
                font-weight: 600;
                // letter-spacing: 1px;
              }
              &:hover {
                background-color: #dadcde;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .theViettel {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .content {
      .content-silde {
        margin-top: 50px;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        align-self: center;
        gap: 30px;
        color: white;
      }
    }
  }
}
@media screen and (max-width: 400px) {
  .theViettel {
    .content {
      .content-silde {
        .silde1 {
          flex-direction: column;
        }
        .silde2 {
          flex-direction: column;
        }
      }
    }
  }
}
