.Footer-title {
  text-align: center;
  // margin-bottom: 30px;
  height: 30px;
  width: 100%;
  padding: 10px;
  background-color: rgb(0, 153, 153, 1) !important;
  color: #e7e7e7;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Footer {
  display: flex;
  align-items: center;
  justify-content: center;
  // margin-top: 100px;
  // background-color: #e7e7e7;
  background-image:linear-gradient(
    to bottom,
    rgb(236, 231, 231),
    rgba(236, 236, 244, 0.5)
  ), url(../../Image/background2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  // background-position: center;
  .flex {
    width: 100%;
    display: flex;
    align-items: center;
    // justify-content: center;
    padding: 25px;
    flex-direction: column;
    .Footer-content-flex {
      justify-items: center;
        width: 100%;
      margin-top: 20px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-self: center;
      gap: 20px;
      .Footer-content {
        padding: 10px;
        width: 100%;
        height: 200px;
        max-width: 520px;
        background-color: white;
        border-radius: 15px;
        .Footer-content-title {
            text-align: center ;
        //   color: rgb(0, 153, 153, 1) !important;
          text-decoration: underline;
          margin-bottom: 30px;
        }
        .Footer-content-help {
            strong {
                color: rgb(0, 153, 153) !important;
            }
            ul {
                margin-left: 30px;
                display: grid;
                gap: 15px;
                color: rgb(23 24 35 / 65%);
                font-size: 17px;
                font-weight: 400;
                line-height: 1.5em;
                letter-spacing: -0.3px;
            }
        }
      }
      .Footer-service {
        padding: 10px;  
        width: 100%;
        height: 200px;
        max-width: 520px;
        background-color: white;
        border-radius: 15px;
        .Footer-service-title {
            margin-bottom: 30px;
            text-align: center ;
        //   color: rgb(0, 153, 153, 1) !important;
          text-decoration: underline;
        }
        .Footer-service-other {
            ul {
                margin-left: 20px;
                display: grid;
                gap: 5px;
                color: rgb(23 24 35 / 65%);
                font-size: 17px;
                font-weight: 400;
                line-height: 1.5em;
                letter-spacing: -0.3px;
                li {
                    svg {
                        color: black;
                    }
                    display: flex;
                    align-items: center;
                    list-style: none;
                    gap: 10px;
                    div {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 10px;
                        font-weight: 700;
                        width: 25px;
                        height: 15px;
                        background-color: rgb(238, 0, 51);
                        color: white;
                    }

                }
            }
        }
      }
    }
  }
}

@media screen and (max-width: 810px) {
  .Footer {
    .flex {
      .Footer-content-flex {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
}
@media screen and (max-width: 900px) {
    .Footer {
        .flex {
          .Footer-content-flex {
            grid-template-columns: repeat(1, 1fr);
            justify-content: center;
            align-items: center;
            place-items: center; 
          }
        }
      }
}
@media (max-width: 460px) { 
    .Footer {
        .flex {
            padding-left: 0;
            padding-right: 0;
          .Footer-content-flex {
            
          }
        }
      }
}
@media (max-width: 410px) { 
    .Footer {
        .flex {
          .Footer-content-flex {
            .Footer-content {
                .Footer-content-help {
                    ul {
                        gap: 0;
              
                    }
                }
              }
          }
        }
      }
}

